<template> 
  <div class="app-container">
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>客户信息</span>
      <el-button size="mini" class="btn-add" @click="handleResetAdd()" style="margin-left: 20px">重置</el-button>
    </el-card>
    <div class="table-container" >
      <el-form :model="customer" :rules="rules" ref="customerInfoForm"  label-width="15%" style="width: 70%;margin: auto" size="small">
        <el-form-item label="名字：" prop="customerName">
          <el-input v-model="customer.customerName"></el-input>
        </el-form-item>
        <el-form-item label="联系方式：" prop="customerTel">
          <el-input v-model="customer.customerTel"></el-input>
        </el-form-item>
        <el-form-item label="备注：" prop="other">
          <el-input v-model="customer.other"
                    type="textarea"
                    :rows="5"></el-input>
        </el-form-item>
        <el-form-item style="text-align: center">
          <el-button type="primary" size="medium" :loading="updateLoading" :disabled="updateLoading" @click="handleAddCustomer('customerInfoForm')">添 加</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
  import {updateStockList} from "@/api/stock";
  import {addCustomer, updateCustomer} from "@/api/customer";
  export default {
    name: 'addCustomer',
    data() {
      return {
        listLoading: false,
        updateLoading: false,

        defaultCustomer:{
          customerName: null,
          customerTel: null,
          other: null
        },
        customer : {},
        rules: {
          customerName:  [{required: true, message: '请输入', trigger: 'blur'}],
          customerTel: [{required: true, message: '请输入', trigger: 'blur'}],
          other: [{required: false, message: '请输入', trigger: 'blur'}],
        }
      }
    },
    created() {
      this.customer =  Object.assign({}, this.defaultCustomer);
    },
    methods: {
      handleResetAdd() {
        this.customer =  Object.assign({}, this.defaultCustomer);
      },

      handleAddCustomer(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('是否要进行添加', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.updateLoading = true;
              addCustomer(this.customer).then(response => {
                this.$message({
                  message: '添加成功',
                  type: 'success',
                  duration: 1000
                });
                this.handleResetAdd();
                this.updateLoading = false;
              });
            });
          }
        });
      },
    }
  }
</script>
<style>
</style>



